.name {
    font-weight: 800;
    font-size: 48px;
    text-transform: capitalize;
    color: #184946;

}

.info {
    font-weight: 800;
    color: #184946;

}

.wrapper {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    gap: 50px;
    height: 100vh;

}

.wrapper .other {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.Inactive .wrapper {
    display: none;
}